.recalls-section{
  padding-top: 91px;
  background-color: #F4F5F7;

  &__slider-nav{
    position: absolute;
    right: 0;
    bottom: 0;
  }
}

.recalls-container{
  display: flex;
  justify-content: space-between;
}

.recalls{
  &__content{
    max-width: 612px;
    overflow: hidden;
  }
  &__image{
    margin-bottom: -61px;
    margin-right: 4px;
    position: relative;
    z-index: 5;
  }
  &__items{
    position: relative;
    padding: 59px 0 12px 0;

    .swiper-wrapper{
      height: 249px;
    }
  }
}



.recalls-item{
  &__img{
    overflow: hidden;
    margin-bottom: 25px;
    border-radius: 100%;
    width: 72px;
    height: 72px;
    img{
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__descr{
    margin: 0 0 25px 0;

    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 160%;

    color: var(--color-grey);
  }
  &__author{
    display: block;
    padding-right: 110px;
    margin-bottom: 4px;

    font-style: normal;
    font-weight: 900;
    font-size: 16px;
    line-height: 160%;

    color: var(--color-darkStorm);
  }
  &__author-position{
    display: block;
    padding-right: 110px;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;

    color: var(--color-storm);
  }
}


