.partners-section{
  background-color: #F4F5F7;
  margin-left: -4px;
  .title{
    padding-bottom: 60px;
  }

  &__interior-design{
    padding-top: 121px;
    padding-bottom: 120px;
  }

  &__index{
    padding-top: 81px;
    padding-bottom: 90px;
  }

  &__ourWork{
    padding-top: 170px;
